.loc-contact {
  position: fixed;
  bottom: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  backdrop-filter: blur(15px);
  background-color: #ffffffdd;

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    border: 1px solid #dddddd;
    // padding: 20px 0;
    font-size: 46px;
    line-height: 62px;
    color: cornflowerblue;
    text-decoration: none;
  }
}
