.loc-config-lang {
  display: flex;
  flex-direction: column;
  gap: 12px;

  &__button {
    border: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    gap: 0 12px;
    padding: 8px;
    font-family: "Inter", serif;
    letter-spacing: 0.6px;

    &--active {
      background-color: cornflowerblue;
    }
  }

  &__icon {
    width: 30px;
    height: 30px;
  }
}
