.loc-modal {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(10px);
  background-color: rgba(0, 0, 0, 0.7);

  &__body {
    background-color: #fff;
    padding: 12px;
    width: 90%;
    height: 80%;
  }

  &__header {
    position: relative;
  }

  &__title {
    margin-top: 0;
    margin-bottom: 16px;
  }

  &__close {
    position: absolute;
    top: -5px;
    right: -5px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35px;
    height: 35px;
    border: none;
    font-family: cursive, "Inter", sans-serif;
    font-size: 24px;
    color: #000;
    background-color: transparent;
  }
}
