.loc {
  &__header {
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    height: 160px;
    padding: 20px 20px 15px;
    background-color: #fff;
    transition: all 0.3s ease-in-out;

    &--mini {
      height: 60px;
      padding: 0 20px;
      border-bottom: 1px solid #dddddd;
    }
  }

  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font-size: 20px;
    text-transform: uppercase;
    text-align: center;
    margin: 0;
    padding: 0;
    font-weight: 500;
    color: #333;

    &-h2 {
      height: auto;
      margin-top: 0;
      margin-bottom: 12px;
    }
  }

  &__logo {
    width: 70px;
    transition: all 0.3s ease-in-out;
    position: absolute;
    left: calc(50% - 35px);
    top: 0;

    &--mini {
      top: 8px;
      left: 20px;
      width: 30px;
    }

    &-name {
      width: 78px;
      transition: all 0.3s ease-in-out;

      &--mini {
        margin-top: 0;
      }
    }

    &-slogan {
      width: 150px;
      transition: all 0.3s ease-in-out;

      &--hidden {
        opacity: 0;
      }
    }
  }
}
